import React, { Component } from 'react';
import {Switch,Route,BrowserRouter as Router} from 'react-router-dom';
import Banner from './Banner';
import Navbar from './Navbar';
import Market from './Market';
import About from './About';
import './Container.sass'
import Cart from './Cart';
import HousesProvider from '../repository/houses';

export default class Container extends Component {
    constructor(props){
        super(props);
        this.state={
            pageId:1
        }
    }

    

    changePageHandler(id){
        this.setState({pageId:id});
    }

    render() {
        return (
            <Router>
            <div className="container-fluid" >
                <div className="row">
        	        <div className="col-sm-5 left-wrapper">
                        <Banner banner='/assets/images/event-banner.jpg'/>
                    </div>
                    <div className="col-sm-7 right-wrapper">
                        <div className="event-ticket-wrapper">
                            <div className="event-tab">
                                <Navbar changePageHandler ={this.changePageHandler.bind(this)}/>
                                <div className="tab-content">
                                    <div role="tabpanel" className="tab-pane fade in active" id="buyTicket">
                                        <div className="row">
                                            
                                                <Switch>
                                                        <Route path="/" exact>
                                                            <HousesProvider>
                                                                <Market/>
                                                            </HousesProvider>
                                                        </Route>
                                                        <Route path="/about">
                                                            <About/>
                                                        </Route>
                                                    
                                                </Switch>
                                            
                                            
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div id="cart">
                                <HousesProvider>
                                    <Cart/>
                                </HousesProvider>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            </Router>
        )
    }
}
