import React, {useContext} from 'react'
import {HousesRepository} from '../repository/houses';
import Item from './Item'

export default () => {

        const {houses} = useContext(HousesRepository);
        
        console.log(houses)
        
        let col0=[];
        for (let i=0; i < houses.length; i+=2){
            console.log(houses[i].id,houses.length)
            col0.push(houses[i]);
        }

        let col1=[];
        for (let i=1; i < houses.length; i+=2){
            console.log(houses[i].id,houses.length)
            col1.push(houses[i]);
        }
        
        return (
            <div className="col-md-12">
                <div className="row">
                    <div className="col-md-6">
                        {col0.map(house=><Item key={house.id} id={house.id} imageUrl={house.imageUrl} price={house.price}/>)}
                    </div>
                    <div className="col-md-6">
                        {col1.map(house=><Item key={house.id} id={house.id} imageUrl={house.imageUrl} price={house.price}/>)}
                    </div>
                </div>
                
                
            </div>
        )
    }

