import React, { Component } from 'react'

export default class Cart extends Component {
    constructor(props){
        super(props)
        this.state={
            items:{}
        };
    }

    getTotalPrice(){
        let ret = 0;
        Object.values(this.state.items).map(a=> ret+=a.price * a.quantity);
        return ret;
    
    }
    getTotalQuantity(){
        let ret = 0;
        Object.values(this.state.items).map(a=> ret+= a.quantity);
        return ret;
    }

    updateItem(item){
        let state = {...this.state};
        state.items[item.id] = item;
        console.log(state)
        this.setState(state)
    }

    render() {
        return (
            <div className="cart">
                <div className="row">
                    <div className="col-xs-6">
                        <p> 
                <span className="ticket-count">{this.getTotalQuantity()}</span> Items <span className="divider"></span>
                    Total: Rs. <span className="total-amount">{this.getTotalPrice()}</span></p>
                    </div>
                    <div className="col-xs-6">
                    	<div className="text-right">
                        	<a href="/" className="btn disabled" data-toggle="modal" data-target="#ticket-details">PROCEED</a>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
