import React, { Component } from 'react'
import './Item.sass';

export default class Item extends Component {

    constructor(props){
        super(props)
        this.state = {
            quantity : 0
        };
    }

    incressQuantity(){
        this.updateValue.bind(this)(this.state.quantity + 1);
    }

    decressQuantity(){
        this.updateValue.bind(this)(this.state.quantity - 1);
    }

    changeValue(e){
        console.log("changed");
        this.updateValue(Number(e.target.value));
    }

    updateValue(value){

        this.setState({quantity : value});
        
        const item = {...this.props,quantity:value};

        window.cart.updateItem(item);
    }
    

    render() {

        return (
            <div className="row">
                <div className="col-md-12">
                    <div className="ticketBox" data-ticket-price="50000">
                        <div className="inactiveStatus"></div>
                        
                        <div className="row">
                            <div className="col-xs-6">
                                <div className="ticket-name">Platinum
                                    <span>Standing<br/>1 Ticket for 1 person</span>
                                </div>
                            </div>
                        
                            <div className="col-xs-6">
                                <div className="ticket-price-count-box">
                                    <p className="price">${this.props.price}</p>
                                </div>
                            </div>
                        </div>

                        <div className="row">
                            <div className="ticket-image" >
                                <img alt="" src={this.props.imageUrl} style={{
                                    width:'100%',
                                    }}/>
                            </div>

                        </div>
                        
                        
                        <div className="ticket-description">
                            <p><strong>Please refer Venue Plan:</strong><br/>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam</p>
                        </div>
                                
                    </div>
                </div>
            </div>)
        
    }
}
